import React from "react"

import styles from "./project.module.css"

const Project = props => (
  <li className={styles.projectItem}>
    <div className={styles.projectDescription}>
      <a className={styles.projectHeader} href={props.projectURL}>
        <h1>{props.projectTitle}</h1>
      </a>
      <div className={styles.projectImage}>
        <img src={props.projectImg} alt={props.projectTitle + " image"} />
      </div>
      <ul>
        <li>
          <strong>Date:</strong> {props.projectDate}
        </li>
        <li>
          <strong>URL:</strong>{" "}
          <a href={props.projectURL} target="_blank" rel="noopener noreferrer">
            {props.projectURL}
          </a>
        </li>
        <li>
          <strong>Technologies Used:</strong> {props.techUsed}
        </li>
        {props.githubURL && (
          <li>
            <strong>Source:</strong>{" "}
            <a href={props.githubURL}>{props.githubURL}</a>
          </li>
        )}
        <li>
          <strong>Description: </strong>
          {props.description}
        </li>
      </ul>
    </div>
  </li>
)

export default Project
